<template>
  <div>
    <nav class="navbar navbar-expand-lg navbar-light ">
      <a class="navbar-brand" href="#">
        <img class="d-inline-block align-top" src="../assets/hunggwo-logo-only.svg" height="30" alt="">
        <span style="color: black;"> </span>
        <span class="d-none d-sm-none d-md-inline-block"><small></small></span>
      </a>
      
      
      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>

      <div class="collapse navbar-collapse " id="navbarSupportedContent">
        <ul class="navbar-nav ml-auto d-sm-block d-md-block d-lg-none d-xl-none">
          <li class="nav-item" v-for="item in category">
            <a class="nav-link" data-toggle="collapse" data-target=".navbar-collapse.show" @click="currentItem = item">{{ item.name }}</a>
          </li>
        </ul>
      </div>
    </nav>
    <div class="row">
      <div class="col-lg-3">
        <ul class="list-group d-none d-sm-none d-md-none d-lg-block">
          <li :class="{ 'list-group-item': true, 'active': item === currentItem }" 
              v-for="item in category" 
              :key="item.name"
              @click="currentItem = item">{{item.name}}</li>
        </ul>
      </div>
      <div class="col-lg-9" style="padding: 0 5%">
        <h3 class="text-center">{{ currentItem.name }}</h3>
        <hr>
        <carousel
        :autoplay="false"
        :perPage="1"
        :navigationEnabled="true">
          <slide 
          class="slide"
          v-for="n in currentItem.photos"
          :key="n.photos"
          >
            <div class="img-card">
              <img :src="`/static/${currentItem.name}/${n}.jpg`" alt="" >
            </div>
          </slide>
        </carousel>
      </div>
    </div>
    
  </div>
</template> 

<script>
import { Carousel, Slide } from 'vue-carousel';

export default {
  name: 'app',
  data() {
    return {
      currentItem: '',
      category: [
        {
          name: '',
          photos: 10,
        },
        {
          name: '',
          photos: 4,
        },
        {
          name: '',
          photos: 3,
        },
        {
          name: '',
          photos: 15,
        },
        {
          name: '',
          photos: 9,
        },
        {
          name: '',
          photos: 2,
        },
        {
          name: '',
          photos: 3,
        },
        {
          name: '',
          photos: 5,
        },
        {
          name: '',
          photos: 10,
        },
        {
          name: '',
          photos: 4,
        },
        {
          name: '',
          photos: 6,
        },
        {
          name: '',
          photos: 3,
        },
        {
          name: '',
          photos: 17,
        },
        {
          name: '',
          photos: 4,
        },
      ],
    };
  },
  mounted() {
    this.currentItem = this.category[0];
  },
};
</script>

<style lang="sass">
body
  background-image: url("../assets/intersection.png")
  background-color: rgba(0, 0, 0, 0.3)

.slide
  display: flex
  justify-content: center
  align-items: center

.img-card
  padding: 1% 2%;
  max-height: 75vh
  // border: 1px solid rgba(0,0,0,0.2)
  display: flex
  justify-content: center
  // box-shadow: 0 16px 24px 2px rgba(0,0,0,.14), 0 6px 30px 5px rgba(0,0,0,.12), 0 8px 10px -5px rgba(0,0,0,.2);
  @media (max-width: 768px)
    display: block
  img
    box-shadow: 0 4px 5px 0 rgba(0,0,0,.14), 0 1px 10px 0 rgba(0,0,0,.12), 0 2px 4px -1px rgba(0,0,0,.2)

.VueCarousel-wrapper
  *
    @media (max-width: 768px)
      width: 100%
</style>



